import * as React from "react";
// import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import { IoClose } from "react-icons/io5";
import styled from "styled-components";
import { course, courseArray } from "../../../data/Subject";
import { theme } from "../../../data/ThemeColor";
import {
  handleError,
  handleSuccess,
  handleWarning,
} from "../../../utils/Toastify";
import useAxios from "../../../api/Axios";

const EditClass = ({ onReload, open, setOpen, info }) => {
  const axios = useAxios();
  const [classInfo, setClassInfo] = React.useState(null);
  const [loading, setLoading] = React.useState(true);
  React.useEffect(() => {
    if (classInfo === null) {
      setClassInfo(info);
    }
    setLoading(false);
  }, [info, classInfo, setClassInfo]);

  const handleClose = () => {
    setOpen(false);
  };

  const handleSubmit = (evt) => {
    evt.preventDefault();
    let array = [];
    for (const [key, value] of Object.entries(classInfo)) {
      if (value === null || value === undefined || value === "") {
        array.push(key);
      }
    }
    if (array.length === 0) {
      try {
        const url = "/class/update?id=" + info.classId;
        axios
          .patch(url, classInfo)
          .then((res) => {
            onReload();
            if (res.status === 200) {
              handleSuccess(res.data);
            } else {
              handleWarning(res.data);
            }
            setOpen(false);
          })
          .catch((err) => {
            handleError(
              err?.response?.data?.message || err?.response?.data?.sqlMessage
            );
          });
      } catch (error) {
        handleError(
          error?.response?.data?.message || error?.response?.data?.sqlMessage
        );
      }
    } else {
      handleError(`' ${array.toString()} ' is required`);
    }
  };

  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setClassInfo({ ...classInfo, [name]: value });
  };

  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        {loading ? (
          <Loading />
        ) : (
          <React.Fragment>
            <Header>
              <HeaderText>Update Class Information</HeaderText>
              <IconBox onClick={handleClose}>
                <IoClose />
              </IconBox>
            </Header>
            <DialogContent
              style={{
                display: "flex",
                flexWrap: "nowrap",
                alignItems: "center",
              }}
            >
              <Form onSubmit={handleSubmit}>
                <SelectGroup>
                  <InputGroup>
                    <Label>Year</Label>
                    <Select
                      name="year"
                      defaultValue={classInfo.year}
                      onChange={(e) => {
                        handleChange(e);
                        delete classInfo[course];
                      }}
                      style={{ marginRight: "10px" }}
                      required
                    >
                      <option value="1">1st Year</option>
                      <option value="2">2nd Year</option>
                      <option value="3">3rd Year</option>
                      <option value="4">4th Year</option>
                    </Select>
                  </InputGroup>
                  <InputGroup>
                    <Label>Semester</Label>
                    <Select
                      name="semester"
                      defaultValue={classInfo.semester}
                      onChange={(e) => {
                        handleChange(e);
                        delete classInfo[course];
                      }}
                      required
                    >
                      {classInfo.year !== undefined && (
                        <>
                          <option value="1">1st Semister</option>
                          <option value="2">2nd Semister</option>
                        </>
                      )}
                    </Select>
                  </InputGroup>
                </SelectGroup>
                <InputGroup>
                  <Label>Course</Label>
                  <Select
                    name="courseName"
                    defaultValue={classInfo.courseName}
                    onChange={(e) => {
                      const val = e.target.value;
                      if (val !== "") {
                        courseArray[Number(classInfo.year) - 1][
                          Number(classInfo.semester) - 1
                        ].map((data) => {
                          if (data.fullTitle === val) {
                            setClassInfo({
                              ...classInfo,
                              courseType: data.courseType,
                              courseName: val,
                            });
                          }
                          return null;
                        });
                      } else {
                        console.log("first course");
                        setClassInfo({ ...classInfo, courseName: "" });
                      }
                    }}
                    required
                  >
                    {classInfo.year !== undefined &&
                      classInfo.semester !== undefined && (
                        <option value=""></option>
                      )}
                    {classInfo.year !== undefined &&
                    classInfo.semester !== undefined
                      ? courseArray[Number(classInfo.year) - 1][
                          Number(classInfo.semester) - 1
                        ].map((data) => {
                          return data.year === Number(classInfo.year) &&
                            data.semester === Number(classInfo.semester) ? (
                            <option key={data.code} value={data.fullTitle}>
                              {data.fullTitle}
                            </option>
                          ) : null;
                        })
                      : null}
                  </Select>
                </InputGroup>

                <InputGroup>
                  <Label>Session</Label>
                  <Input
                    name="session"
                    type="text"
                    defaultValue={classInfo.session}
                    onChange={handleChange}
                    placeholder="2018-19"
                    required
                  />
                </InputGroup>

                <InputGroup>
                  <Label>Term</Label>
                  <Input
                    name="term"
                    type="number"
                    defaultValue={classInfo.term}
                    onChange={handleChange}
                    placeholder="Term"
                    required
                  />
                </InputGroup>
                <InputGroup>
                  <Label>Color</Label>
                  <SelectColor
                    classInfo={classInfo}
                    setClassInfo={setClassInfo}
                  />
                </InputGroup>
                <ButtonGroup>
                  <Button
                    type="button"
                    onClick={() => setOpen(false)}
                    style={{ background: "rgba(255, 56, 56,1.0)" }}
                  >
                    Cancel
                  </Button>
                  <Button
                    type="submit"
                    style={{ background: "rgba(72, 52, 212,1.0)" }}
                  >
                    Update
                  </Button>
                </ButtonGroup>
              </Form>
            </DialogContent>
          </React.Fragment>
        )}
      </Dialog>
    </div>
  );
};

export default EditClass;

function SelectColor({ classInfo, setClassInfo }) {
  const handleColorChange = React.useCallback(
    (id) => {
      setClassInfo({ ...classInfo, theme: id });
    },
    [classInfo, setClassInfo]
  );

  return (
    <ColorContainer>
      <ColorPanel>
        {theme.map((data, idx) => {
          return (
            <ColorItem
              key={data.id}
              barient={idx === classInfo.theme ? data.color : null}
              onClick={() => handleColorChange(data.id)}
            >
              <Color barient={data.color}>
                <p style={{ marginBottom: "0" }}>{data.short}</p>
              </Color>
            </ColorItem>
          );
        })}
      </ColorPanel>
    </ColorContainer>
  );
}

const Header = styled.div`
  min-width: 300px;
  height: 45px;
  background: rgba(12, 36, 97, 1);
  display: flex;
  flex-wrap: nowrap;
  padding: 0.3rem 0.5rem;
  justify-content: space-between;
  align-items: center;
`;

const HeaderText = styled.p`
  font-weight: 500;
  font-size: 1.1rem;
  color: #fff;
  margin-bottom: 0px;
  padding: 0.2rem 0.5rem;
`;

const IconBox = styled.div`
  width: 35px;
  height: 25px;
  border-radius: 3px;
  outline: 2px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 1.5rem;

  &:hover {
    background: red;
    outline: 2px solid red;
  }
`;

export const Form = styled.form`
  padding: 1rem 2rem;
  width: 450px;
  border-radius: 15px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem 0;
  background: #fff;

  @media (max-width: 460px) {
    width: 100%;
    border: none;
  }
`;

export const FormHeader = styled.h1`
  @media (min-width: 540px) {
    font-size: 1.3rem;
  }
`;

export const InputGroup = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

export const Label = styled.label`
  font-size: 0.9rem;
  font-weight: 600;
`;
export const Input = styled.input`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(95, 39, 205, 01);


  &:,
  &:focus {
    outline: none;
    border: 1px solid rgba(95, 39, 205, 0.5);
  }
`;

export const Select = styled.select`
  padding: 0.5rem 1rem;
  border: 1px solid rgba(95, 39, 205, 01);

  &:focus {
    outline: none;
    border: 1px solid rgba(95, 39, 205, 0.5);
  }
`;

export const SelectGroup = styled.div`
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  width: 100%;
`;

export const Span = styled.span`
  background: rgba(95, 39, 205, 01);
  color: #fff;
  border: 1px solid rgba(95, 39, 205, 01);
`;

export const ButtonGroup = styled.div`
  display: flex;
  flex-wrap: wrap;
  margin-top: 0.5rem;
  width: 100%;
  justify-content: flex-end;

  @media (max-width: 560px) {
    flex-direction: column-reverse;
  }
`;

export const Button = styled.button`
  border: none;
  padding: 0.5rem 1rem;
  font-weight: 600;
  min-width: 100px;
  color: #fff;
  font-size: 0.9rem;
  margin: 0.5rem;
  border-radius: 5px;
  &:nth-last-child(1) {
    margin-right: 0;
  }

  @media (max-width: 560px) {
    padding: 0.5rem 1rem;
    min-width: 95%;
    margin: 0.5rem 0;
    font-size: 1.1rem;
  }
`;

const ColorContainer = styled.div`
  width: 100%;
  height: auto;
  padding: 0.1rem 0.5rem;
  position: relative;
  cursor: pointer;
  background: #fff;
  overflow: hidden;
`;

const ColorPanel = styled.div`
  width: 100%;
  height: auto;
  padding: 0.3rem;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  background: rgba(250, 250, 250, 1);
  margin-bottom: 0.5rem;
  border-radius: 3px;
`;

const ColorItem = styled.div`
  width: 34px;
  height: 34px;
  padding: 2px;
  margin: 3px;
  cursor: pointer;
  border-radius: 50%;
  color: #fff;
  outline: 3px solid
    ${(props) => (props.barient ? props.barient : "transparent")};
`;

const Color = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: ${(props) => (props.barient ? props.barient : "transparent")};
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  font-weight: 600;
`;

function Loading() {
  return (
    <div
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        padding: "1rem",
      }}
    >
      <span
        style={{
          padding: "0.5rem 1rem",
          borderRadius: "5px",
        }}
      >
        Loading...
      </span>
    </div>
  );
}
